.regionsContainer {
    padding: 10px 30px;
    width: 400px;
    display: flex;
    flex-direction: column;
    color: white;
    background-color: #090909;
}

.regionContainer {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-bottom: solid 1px white;
    text-align: center;
}

.controlPanel {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px;
}

.regionId {
    width: 30px;
}

.regionName {
    width: 120px;
}

.regionCode {
    width: 70px;
}

.regionButton {
    width: 16px;
}

.regionNameInput {
    width: 112px;
}

.regionCodeInput {
    width: 62px;
}